import React from 'react';
import './footer.scss';

const Footer = ({links, logos}) => {
    const logoImg = logos.map((logoItem, i) => <img className="footer__logo-item" src={logoItem} alt="partners logo" key={i}/>);
    const navLinks = links.map((link, i) => <a className="footer__links-item" href={link.href} key={i}>{link.title} </a>);

    return (
        <footer className="footer">
            <div className="container">
                <div className="footer__logo">
                    {logoImg}
                </div>
                <div className="footer__wrap">
                    <div className="footer__links">
                        {navLinks}
                    </div>
                    <p className="footer__about">Сайт был разработан <span>Еленой Козловой</span> в учебных целях, в рамках
                        образовательной программы IT nation. Дизайн сайта создан <span>Валерией Бубырь</span>.</p>
                </div>
                <div className="footer__copy">
                    <p className="">2020, Все права защищены</p>
                    <a href="#" className="">Согласие на обработку персональных данных </a>
                    <a href="#" className="">Политика конфиденциальности</a>
                </div>
            </div>
        </footer>
    );
};

export default Footer;