import React, {useState} from 'react';

const SectorItem = ({sector}) => {
    const [isDescShow, setDescShow] = useState(false);
    const toggleDist = () => {
        setDescShow(
            !isDescShow
        )
    };

    return (

        <div className="sectors__item"
             onClick={toggleDist}
             onMouseLeave={() => setDescShow(false)}
             style={{
            background: `url(${sector.imgUrl}) center no-repeat`,
            backgroundSize: "cover"
        }}>
            <p className="sectors__name">
                {sector.name}
            </p>
            {
                isDescShow && <p className="sectors__text">
                    {sector.desc}
                </p>
            }
        </div>
    );
};

export default SectorItem;

