import React from 'react';

import SliderFacts from "./sliderFacts";

import SwiperCore, {Navigation, Pagination} from 'swiper';
import {Swiper, SwiperSlide} from 'swiper/react';

import 'swiper/swiper.scss';
import 'swiper/components/navigation/navigation.scss';
import 'swiper/components/pagination/pagination.scss';

import './facts.scss';

SwiperCore.use([Navigation, Pagination]);

const Facts = ({title, slides}) => {
    const factsSlider = slides.map((slide) => <SwiperSlide key={slide.id}>
        <SliderFacts item={slide}/>
    </SwiperSlide>);

    return (
        <section className="facts" id="facts">
            <div className="container">
                <h2 className="facts__title">{title}</h2>
                <Swiper
                    pagination={{clickable: true}}

                    breakpoints={
                        {
                            480: {
                                slidesPerView: 1,
                                slidesPerGroup: 1,
                                spaceBetween: 0,
                                autoHeight: true,
                            },
                            990: {
                                slidesPerView: 3,
                                slidesPerGroup: 3,
                                spaceBetween: 40,
                                autoHeight: false,
                                navigation: {
                                    nextEl: ".swiper-button-prev",
                                    prevEl: ".swiper-button-next",
                                }
                            },
                        }
                    }
                >
                    {factsSlider}
                </Swiper>
                <div className="swiper-button-prev"/>
                <div className="swiper-button-next"/>
            </div>
        </section>
    );
};

export default Facts;
