import React from 'react';
import './style/header.scss';
import './style/partners.scss';
import './style/nav.scss';

const Header = ({title, links, logos}) => {
    const logoImg = logos.map((logoItem, i) => <img className="partners__item" src={logoItem} alt="partners logo"
                                                    key={i}/>);
    const navLinks = links.map((link, i) => <a className="nav__item links--line" href={link.href} key={i}>{link.title} </a>);

    return (
        <section className="header">
            <div className="container">
                <div className="header__wrap">
                    <div className="header__partners partners">
                        {logoImg}
                    </div>
                    <nav className="header__nav nav">
                        <details>
                            <summary className="nav__btn">open</summary>
                            <div className="nav__body">
                                <button className="nav__btn--close"/>
                                <div className="nav__links">
                                    {navLinks}
                                </div>
                                <div className="nav__copy">
                                    <p className="">2020, Все права защищены</p>
                                    <p className="">Согласие на обработку персональных данных </p>
                                    <a href="#" className="">Политика конфиденциальности</a>
                                </div>
                            </div>
                        </details>
                    </nav>
                </div>
                <h1 className="header__title">{title}</h1>
                <a className="header__btn" href="#">Отследить</a>
            </div>
        </section>
    );
};

export default Header;