import React from 'react';
import "./sliderFacts.scss";

const SliderFacts = ({item}) => {
    return (
        <div className="slider__item">
            <div className="slider__img">
                <img className="" src={item.imgUrl} alt="slide"/>
            </div>
            <p className="slider__desc">{item.desc}</p>
        </div>
    );
};

export default SliderFacts;